@import url(https://fonts.googleapis.com/css2?family=Lato:wght@300;400;700&display=swap);
:root {
  font-size: 16px;
}

html, body, #root {
  height: 100%;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body, input, button, textarea {
  font: 400 1rem Lato, sans-serif;
}

body {
  -webkit-font-smoothing: antialiased !important;
}

:root {
  --color-background: #fff;
  --color-primary: #2D68FF;
  --color-secondary: #000;

  --color-error: #EC1111;
  --color-success: #0FC000;
  --color-warning: #E9EE09;

  --header-height: 164px;
}

.main-content header {
  position: fixed;
  display: flex;
  width: 100%;
  flex-direction: column;
  height: 164px;
  height: var(--header-height);
  background: #fff;
  background: var(--color-background);
}

.main-content main {
  padding-top: calc(164px + 16px);
  padding-top: calc(var(--header-height) + 16px);
  padding-bottom: 16px;
}
.navbar-component-content {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  padding: 16px;
  border-bottom: 1px solid;
}
.user-avatar-content {
  width: 2rem;
  height: 2rem;
  border-radius: 50%;
  border: 2px solid var(--color-primary);
  padding: 10px;
}

.user-avatar-content img {
  width: 100%;
  height: 100%;
}
.primary-button-content {
  display: flex;
  min-width: 100px;
}

.primary-button-content button:hover {
  cursor: pointer;
}
.text-input-component-content {
  display: flex;
  min-width: 272px;
  max-width: 500px;
}

.text-input-component-content input {
  width: 100%;
  line-height: 2;
}

.todo-create-component-content {
  display: flex;
  flex-direction: row;
  justify-content: center;
  padding: 16px;
  border-bottom: 1px solid;
  grid-gap: 8px;
  gap: 8px;
}

.todo-item-component-content {
  border: 1px solid;
  padding: 8px;
  min-width: 360px;
  width: 50%;
  min-height: 24px;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  grid-gap: 16px;
  gap: 16px;
}

.todo-item-component-content span {
  word-break: break-word;
}
.todo-item-component-content svg {
  font-size: 1.2rem;
  transition: .5s;
}

.todo-item-component-content svg:hover {
  cursor: pointer;
  color: var(--color-primary);
}
.todo-list-component-content {
  display: flex;
  flex-direction: column;
  align-items: center;
  grid-gap: 12px;
  gap: 12px;
}

ul.todo-list-component-content  {
  padding: 0;
  list-style-type: none;
}
