.todo-item-component-content {
  border: 1px solid;
  padding: 8px;
  min-width: 360px;
  width: 50%;
  min-height: 24px;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  gap: 16px;
}

.todo-item-component-content span {
  word-break: break-word;
}
.todo-item-component-content svg {
  font-size: 1.2rem;
  transition: .5s;
}

.todo-item-component-content svg:hover {
  cursor: pointer;
  color: var(--color-primary);
}